@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  padding: 3.7rem 4rem 0 4rem;
  background-image: linear-gradient(0deg, rgba(247, 238, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
  border-radius: 0.8rem;
  box-shadow: 0 0.1rem 5rem 0 rgba(0, 0, 0, 0.08);
}

.button {
  margin-top: auto;
  height: 5.5rem;
}

.results {
  position: relative;
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3.2rem;
  padding-bottom: 3.3rem;
  overflow: hidden;


  &::before {
    content: "";
    position: absolute;
    bottom: -1.2rem;
    left: 4.1rem;
    width: 18.4rem;
    height: 11.2rem;
    background-image: url("../../../../assets/icons/HUG’S.svg");
    background-size: cover;
  }
}

.label {
  @include fontButtonSimple($colorDarkest);

  margin: 0;
  opacity: 0.26;
}

.value {
  @include h1;

  margin: 0.2rem 0 0 0;
}
