@import "styles/variables";
@import "styles/mixins";

.section {
  padding-top: 10rem;
  padding-bottom: 9.5rem;
  text-align: center;
  background-color: $colorLightest;
}

.heading {
  font-weight: 900;

  span {
    color: $colorHighlightLight;
  }
}

.descriptor {
  @include fontDescriptor;

  margin: 2rem 0 0 0;
  padding: 0 25%;
}

.notification {
  @include fontNotification;

  margin: 3rem 0 0 0;
  padding: 0 10%;
  opacity: 0.8;
}

.triggers {
  @include resetList;

  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1.4rem;
}

.trigger {
  &:not(:first-child) {
    margin-left: 6rem;
  }
}

.button {
  @include resetButton;
  @include fontTrigger;

  position: relative;
  border-bottom: 1px solid transparent;
  opacity: 0.3;
  transition-duration: $transitionDuration;
}

.buttonActive {
  opacity: 1;
  border-bottom-color: $borderTabs;
}

.list {
  @include resetList;

  display: flex;
  margin-top: 4rem;
}
