@import "styles/variables";
@import "styles/mixins";

.section {
  padding-top: 25rem;
  padding-bottom: 4.4rem;
  background-color: black;
  text-align: center;
}

.descriptor {
  @include fontDescriptor($colorLightest);

  margin: 2.2rem 0 0 0;
  padding: 0 20%;
  opacity: 0.7;
}

.hint {
  @include fontDescriptor($colorHighlightLight, 600);

  margin: 7rem 0 0 0;
}
