@import "styles/variables";
@import "styles/mixins";

.section {
  position: relative;
  padding-top: 18.5rem;
  padding-bottom: 22.2rem;
  background-color: $colorLightest;
  border-top: 1px solid $borderTabs;

  &::before {
    content: "";
    position: absolute;
    top: 8.1rem;
    right: calc(50% - 60rem);
    width: 59.7rem;
    height: 62.5rem;
    background-image: url("../../../assets/images/phone_02.png");
    background-size: cover;
    z-index: 0;
  }
}

.container {
  padding: 0 62rem 0 25rem;
}

.heading {
  padding-right: 15%;
}

.descriptor {
  @include fontButtonStrong($colorDarkest, 400);

  margin: 2.5rem 0 0 0;
  padding-right: 20%;
}

.hint {
  @include fontInputLabel(black);

  margin: 2rem 0 0 0;
  opacity: 0.5;
}

.button {
  margin-top: 4.6rem;
  font-weight: 500;
}
