@import "styles/variables";
@import "styles/mixins";

.section {
  position: relative;
  background-color: $colorHighlightDark;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 21.2rem;
    height: 25.4rem;
    background-image: url("../../../assets/images/money_02.png");
    background-size: cover;
  }
}

.container {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 10rem;
    left: 2.1rem;
    width: 22.5rem;
    height: 22rem;
    background-image: url("../../../assets/images/money_01.png");
    background-size: cover;
  }

  &::after {
    content: "";
    position: absolute;
    top: 16.6rem;
    right: 13.4rem;
    width: 17.1rem;
    height: 16.7rem;
    background-image: url("../../../assets/images/money_03.png");
    background-size: cover;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8.8rem 10rem 10rem 10rem;
  text-align: center;
}

.descriptor {
  @include fontDescriptor($colorLightest);

  margin: 2.2rem 0 0 0;
}

.input {
  width: 48.8rem;

  &:first-of-type {
    margin-top: 2.1rem;
  }

  &:not(:first-of-type) {
    margin-top: 1.5rem;
  }
}

.button {
  margin-top: 4.3rem;
  width: 22.5rem;
  font-weight: 500;
}
